.text-servise {
  width: 130px;
  height: 60px;
  margin-left: 615px;
  margin-bottom: 30px;

  h2 {
    font-family: Raleway;
    font-style: normal;
    font-weight: 600;
    font-size: 40px;
    line-height: 60px;
    text-align: center;

    color: #3e3e3e;
  }
  .text-hr {
    width: 82px;
    height: 2px;
    margin-left: 25px;

    background: #e4b56b;
  }
}
.wrp {
  .item-services-main {
    position: absolute;
    margin-left: 90px;
    .item-services-main-grid {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 0px;
      grid-row-gap: 0px;
      .service-bloc-1 {
        grid-area: 1 / 1 / 2 / 2;
      }
      .service-bloc-2 {
        grid-area: 1 / 2 / 2 / 3;
      }
      .service-bloc-3 {
        grid-area: 1 / 3 / 2 / 4;
      }
      .service-bloc-4 {
        grid-area: 2 / 1 / 3 / 2;
      }
      .service-bloc-5 {
        grid-area: 2 / 2 / 3 / 3;
      }
      .service-bloc-6 {
        grid-area: 2 / 3 / 3 / 4;
      }
    }
  }

  .service-bloc-grid2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    .service-bloc-7 {
      width: 585px;
      height: 202px;
      grid-area: 1 / 1 / 2 / 2;
    }
    .service-bloc-8 {
      width: 585px;
      height: 202px;
      grid-area: 1 / 2 / 2 / 3;
    }
  }
}

.text-payment-blokc {
  margin-left: 400px;
  margin-top: 25px;
  width: 397px;
  height: 43px;

  background: #454647;

  h2 {
    padding-top: 10px;
    padding-left: 10px;
    width: 397px;
    height: 23px;
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 23px;
    text-align: center;

    color: #ffffff;
  }
}
